* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.container {
  width: 90%;
  margin: 1rem auto;
}
.searchBox label {
  display: block;
}
.searchBox input {
  width: 100%;
  padding: 0.5rem;
  outline: 0;
}
.card-img {
  width: 100%;
  height: 100%;
  margin-bottom: 1rem;
  transition: all 0.3s;
}
.card-img:hover {
  cursor: pointer;
}
.image-box {
  columns: auto 4;
  gap: 1rem;
}
